.chart-stat-box,
.chart-visualization,
.chart-details-section {
    background: rgba(255, 255, 255, 0.6);
    border: 1px solid rgba(226, 232, 240, 0.6);
    border-radius: 14px;
    padding: 16px;
}

body.dark-forest .chart-stat-box,
