   DASHBOARD COMPONENTS
   NOTA: Estilos de modales movidos a modals-unified.css
   ============================================ */

/* Loading and Empty States */
.loading-state,
.empty-state {
    text-align: center;
    padding: 40px 20px;
    color: #999;
    font-style: italic;
}

/* Client Item */
.client-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 16px;
    border-bottom: 1px solid #eee;
    border-left: 3px solid transparent;
    transition: background 0.2s, border-left-color 0.2s;
}

.client-item:hover {
    background: #f9f9f9;
}

/* Assigned client styling */
.client-item.client-assigned {
    border-left-color: var(--theme-accent-color, #8b2348);
    background: rgba(139, 35, 72, 0.02);
}

.client-item.client-assigned:hover {
    background: rgba(139, 35, 72, 0.05);
}

.client-item:last-child {
    border-bottom: none;
}

.client-info h4 {
    margin: 0 0 4px 0;
    color: #333;
    font-size: 16px;
    display: flex;
    align-items: center;
    gap: 8px;
    flex-wrap: wrap;
}

.client-info p {
    margin: 0 0 8px 0;
    color: #666;
    font-size: 14px;
}

.client-meta {
    display: flex;
    gap: 12px;
    font-size: 13px;
    color: #999;
    flex-wrap: wrap;
}

.client-meta .text-muted {
    color: #999;
    font-style: italic;
}

.client-actions {
    display: flex;
    gap: 8px;
}

