.payment-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 12px 0;
    border-bottom: 1px solid #eee;
}

.payment-item:last-child {
    border-bottom: none;
}

.payment-info {
    display: flex;
    gap: 16px;
    align-items: center;
}

.payment-date {
    color: #666;
    font-size: 14px;
    margin: 0;
}

.payment-amount {
    font-weight: 600;
    font-size: 16px;
    color: #333;
    margin: 0;
}

